.body {
  margin: 0;
  margin-left: 1px;
  margin-right: 1px; }

.container {
  display: block;
  margin: 0 auto;
  padding: 0;
  padding-left: 20px;
  padding-right: 20px; }

@font-face {
  font-family: 'Blogger Sans';
  src: url("../fonts/BloggerSans.ttf"), url("../fonts/BloggerSans.woff"), url("../fonts/BloggerSans.woff2");
  font-style: normal; }

.header {
  margin: 0;
  padding: 0; }

.nav {
  font-family: 'Blogger Sans'; }

.nav__title {
  font-size: 23px;
  font-weight: bold;
  margin-left: 26px; }

.nav__tagline {
  font-size: 36px;
  font-weight: bold;
  line-height: 40px;
  text-align: center;
  width: 238px;
  height: 118px;
  margin-left: 41px; }

.nav__list-navigation {
  list-style: none;
  font-size: 30px; }

.nav__list-navigation a {
  text-decoration: none;
  color: #192144;
  text-transform: uppercase; }

.nav__button-avtorization {
  display: block;
  margin: 0 auto;
  width: 190px;
  height: 40px;
  border-radius: 25px;
  background-color: #FFD748; }

.nav__button-text {
  display: block;
  margin-top: 5px;
  color: #192144;
  font-family: 'Blogger Sans';
  text-transform: uppercase;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold; }

.advantages {
  background-color: #161C35;
  padding-top: 49px;
  padding-bottom: 39px; }

.advantages__citate {
  font-family: Blogger Sans;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  width: 161px;
  height: 128px;
  margin-left: 50px;
  border: 1px solid transparent; }

.advantages__author {
  display: block;
  margin-top: 18px;
  font-weight: bold; }

.advantages__container {
  border: 1px solid transparent;
  width: 270px;
  height: 320px;
  margin: auto; }

.advantages__column-describe {
  position: relative;
  width: 270px;
  height: 160px;
  font-family: Blogger Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: #192144;
  border-radius: 10px;
  border: 1px solid transparent; }

.advantages__first-column {
  background-color: #FF8D30; }

.advantages__twice-column {
  background-color: #FFA530; }

.advantages__third-column {
  background-color: #FFBD30; }

.advantages__fourth-column {
  background-color: #FFD74B;
  width: 270px;
  height: 80px; }

.advantages__twice-column,
.advantages__third-column,
.advantages__fourth-column {
  margin-top: -83px; }

.advantages__describe-text {
  margin: 0;
  margin-top: 22px;
  margin-left: 24px;
  width: 150px;
  height: 34px; }

.advantages__describe-order {
  position: absolute;
  top: 22px;
  right: 21px;
  color: #FFFFFF; }

.about-service {
  height: 353px;
  padding-top: 33px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px; }

.about-service__title {
  margin: 0;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #FFBD30; }

.about-service__describe {
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px;
  width: 211px;
  height: 109px; }

.about-service__text {
  display: block;
  margin-top: 20px;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px; }

.direction {
  padding-top: 71px;
  padding-bottom: 34px;
  background-color: #EDEFF6;
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px; }

.direction__title {
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: #FFBD30;
  margin-left: 30px; }

.direction__describe {
  width: 216px;
  height: 110px;
  margin-top: 19px;
  margin-left: 29px;
  margin-bottom: 60px;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 22px; }

.direction__offer {
  display: flex;
  flex-direction: column-reverse;
  width: 270px;
  height: 190px;
  background-color: yellow;
  border-radius: 10px;
  margin-bottom: 11px; }

.direction__offer-info {
  position: relative;
  display: flex;
  justify-content: space-around;
  width: 270px;
  background-color: #FFFFFF;
  border-radius: 10px; }

.direction__picture {
  position: absolute;
  top: 0;
  left: 20px; }

.direction__title-offer {
  align-self: flex-end;
  margin-bottom: 18px;
  width: 150px;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 21px;
  color: #192144; }

.direction__tagline-offer {
  display: block;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  opacity: 0.3;
  color: #444444; }

.direction__count-people {
  display: flex;
  align-items: center; }

.direction__button {
  position: relative;
  display: block;
  width: 270px;
  height: 62px;
  background-color: #FFD74B;
  border: none;
  border-radius: 10px;
  margin-top: 25px; }

.direction__text-button {
  position: absolute;
  top: 22px;
  left: 23px;
  font-family: 'Blogger Sans';
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px; }
